html {
  scroll-snap-type: mandatory;
  scroll-snap-points-y: repeat(100vh);
  scroll-snap-type: y mandatory;
  width: 100%;
  font-family: 'Podkova', serif;
  font-size: 14px;
  line-height: 1.5;
  color: rgba(0,0,0,.65);

  @media screen and (max-width: 500px) {
    line-height: 1.3;
  }  

}

body {
    /* Full screen width and height */
    width: 100%;
    min-height: 100vh;
  
    /* Centers the container in the middle of the screen */
    display: flex;
    justify-content: center;
    align-items: center;
  
    margin: 0;
    background-color: rgb(245, 245, 244); 

  }
  
  .background {
      position: fixed;
      width: 100vw;
      height: 100vh;
      top:0;
      background: url(../../assets/stucco-White.jpg);
      background-size: 70%;
      opacity: .2;
      z-index: 50;
      box-shadow: inset 0 50px 160px rgba(0,0,0,0.2);
  }
  
  .container {
    position: absolute;
    perspective: 260px;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
  }
  
  .container__inner {
    width: 300px;
    height: 300px;
    transition: transform 0.5s;
    -webkit-transition: transform 0.5s;
    -moz-transition: transform 0.5s;
    -o-transition: transform 0.5s;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    @media screen and (max-width: 500px) {

      margin-bottom: 80px;

    }  
  }

  .container__hover {

    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    transition: all .5s cubic-bezier(0.84, 0, 0.58, 1);
    transform: none;
  }

  .container__hover:hover {

    transform: scale(1.2);
    transition: all .5s cubic-bezier(0.94, 0.01, 0.59, 0.99);

  }
  
  
  .screen {
    width: 100vw;
    height: 100vh;
    scroll-snap-align: start;
    position: relative;
  }

  .btn {
    text-decoration: none;
    padding: 8px 12px;
    border: 2px solid rgba(0,0,0,.65);
    color: inherit;
  }

  .btn-group {
    margin-top: 20px;
  }

  .container__description {
    position: absolute;
    bottom: 20px;
    left: 20px;
    padding: 20px;
    @media screen and (max-width: 500px) {

      bottom: 40px;

    }  
    /*
    .page.chrome .container & {
       opacity: 0;
       transition: all .2s .3s; 
    }

    .page.chrome .container:hover & {

        opacity: 1;
        transition: all .2s .3s;

    }
    */
  }
  
  .description__title {
    max-width: 200px;
    @media screen and (max-width: 500px) {
      max-width: none;
    }  
  }
  
.page {
  background-color: rgb(245, 245, 244); 
  transition: all 1s;
} 
  
  